import React from 'react'
import Button from './Button'  
import'./Form.css' 
import { useState } from 'react'
import Listitems from './Listitems'



function Form() {
   
    const [input,setInput] = useState("");
    const [items,setItems] = useState([]);

    

 
    
     

   const handleChange=(event)=>{
     
        setInput(event.target.value);
         }

         const storeItems=(event)=>{
             event.preventDefault();
            setItems( [...items,{
                id:items.length,
                name:input,
            }]);
           console.log(items);
           setInput("");
         }
        
        function deleteItems(key)
         {
             const removeArr = [...items].filter(item => item.id!==key)
             setItems(removeArr);
            console.log("clicked");
        } 

        function setUpdate(name,key)
        {
          let update = [...items].map(item => {
              if(item.id===key){
              item.name = name; 
            
              }
              return item;
          } )

          setItems(update);
        }
               
                
        


       
          
      
        
        
      
             
            
         
           
                
        

    return (
        <div className="Main__outline">
         <form id="todo-list"  >
             <input  type="text" placeholder="Enter text" value={input} onChange={handleChange}></input>

           <Button onClick={storeItems}  />
            
           
         </form>
         
         <Listitems items={items} deleteItems={deleteItems} setUpdate={setUpdate} />
        
        
      
        </div>
        
    )
}

export default Form
